@import "src/styles/main";

.Container {
  position: fixed;
  top: 24px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  pointer-events: none;
  
  // Theme classes
  &.market-theme {
    @include market-theme;
  }
  
  &.barter-theme {
    @include barter-theme;
  }
  
  &.willing-theme {
    @include willing-theme;
  }
  
  &.map-theme {
    @include map-theme;
  }
}

.NavBar {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 20px;
  border-radius: 32px;
  pointer-events: all;
  
  // Default styling for market and barter
  @include glassmorphism-card;
  backdrop-filter: blur(25px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  background-color: var(--theme-primary, var(--primary-color-2));
  
  // Theme-specific styling
  .Container.market-theme & {
    @include market-glassmorphism-card;
    background: linear-gradient(135deg, 
      rgba(255, 152, 0, 0.95) 0%, 
      rgba(255, 183, 77, 0.9) 100%);
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
    border: 1px solid rgba(255, 152, 0, 0.3);
    box-shadow: 
      0 12px 40px rgba(255, 152, 0, 0.25),
      inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }
  
  .Container.barter-theme & {
    @include glassmorphism-card;
    backdrop-filter: blur(25px);
    background-color: var(--theme-primary, var(--primary-color-2));
  }
  
  .Container.willing-theme & {
    @include willing-glassmorphism-card;
    background: linear-gradient(135deg, 
      rgba(236, 64, 122, 0.95) 0%, 
      rgba(244, 143, 177, 0.9) 100%);
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
    border: 1px solid rgba(236, 64, 122, 0.3);
    box-shadow: 
      0 12px 40px rgba(236, 64, 122, 0.25),
      inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }
  
  .Container.map-theme & {
    @include map-glassmorphism-card;
    background: linear-gradient(135deg, 
      rgba(45, 125, 50, 0.95) 0%, 
      rgba(76, 175, 80, 0.9) 100%);
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
    border: 1px solid rgba(45, 125, 50, 0.3);
    box-shadow: 
      0 12px 40px rgba(45, 125, 50, 0.25),
      inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }
  
  @include fade-in-animation(0.3s);
}

.NavItem {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
  padding: 12px 16px;
  background: transparent;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  min-width: 64px;
  
  &:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
    
    .IconContainer {
      transform: scale(1.1);
    }
    
    .NavLabel {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  &.Active {
    background: rgba(255, 255, 255, 0.2);
    
    .IconContainer {
      transform: scale(1.15);
    }
    
    .NavLabel {
      opacity: 1;
      transform: translateY(0);
      @include text-color-palette("white");
      font-weight: 600;
    }
  }
}

.IconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.NavIcon {
  @include text-color-palette("white");
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

.NavLabel {
  @include font-size-palette("body-3");
  @include text-color-palette("white");
  
  opacity: 0.8;
  font-weight: 500;
  white-space: nowrap;
  transform: translateY(2px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.ActiveIndicator {
  position: absolute;
  bottom: 4px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 8px;
  height: 2px;
  @include bg-color-palette("white");
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.8);
  
  @include expand-animation(0.3s, 0px, 2px);
}

// Responsive Design
@media (max-width: 768px) {
  .Container {
    top: 16px;
  }
  
  .NavBar {
    padding: 10px 16px;
    gap: 4px;
  }
  
  .NavItem {
    padding: 10px 12px;
    min-width: 56px;
    
    .NavLabel {
      @include font-size-palette("body-4");
    }
  }
}

@media (max-width: 480px) {
  .NavBar {
    padding: 8px 12px;
    gap: 2px;
  }
  
  .NavItem {
    padding: 8px 10px;
    min-width: 48px;
    
    .NavLabel {
      display: none;
    }
  }
}
